import React, { useEffect } from "react"
import Glide from "@glidejs/glide"
// import { Images } from '@glidejs/glide/dist/glide.modular.esm'
import "../../node_modules/@glidejs/glide/dist/css/glide.core.min.css"
// import imgs from "./image"
export default function RefCarousel(props) {
  let classRef = React.createRef()
  // const refInput = useRef();
  
  useEffect(() => {
    new Glide(classRef.current, {
      autoplay: "3000",
      hoverpause: false,
      perView: 3,
    }).mount()
  }, [classRef])


  return (
    <div>
      <div className="glide" ref={classRef } style={{marginTop: "10%"}}>
        <div data-glide-el="track" className="glide__track">
          <ul className="glide__slides">
            {props.rollingData.map((nodes,index)=>
              <li className="glide__slide" key={index}>
             {index}
              {/* <img className="glide__slide_img" src={`${nodes.url}?format=webp`} alt={nodes.filename}  style={{ width: "200vw"}}/> */}
            </li>
            )}
          </ul>
        </div>
      </div>
    </div>
  )
}
