import React from "react"

import {graphql} from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Carousel from "../components/refCarousel"

const IndexPage = ({data}) => (
  <Layout >
    <SEO title="Home" />
    <Carousel rollingData={data.allContentstackGatsbyCarousel.edges[0].node.file}/>
    <br/>
    <h2>{data.allContentstackGatsbyhome.edges[0].node.title}</h2>
    <p>{data.allContentstackGatsbyhome.edges[0].node.home_content}</p>
  </Layout>
)
export const pageQuery = graphql`{
  allContentstackGatsbyhome {
    edges {
      node {
        title
        url
        home_content
      }
    }
  }
  allContentstackGatsbyCarousel {
    edges {
      node {
        file {
          filename
          url
        }
      }
    }
  }
  
}`

export default IndexPage
